import React from 'react'

import Container from '../components/container'
import Head from '../components/head'
import Fade from 'react-reveal/Fade'
import Img from 'gatsby-image'
import { graphql } from 'gatsby'

import '../styles/about.css'

class AboutPage extends React.Component {
    render () {
        const { data } = this.props

        return (
            <Container>
                <Head title="About"/>
                <Fade>
                    <div className="heroSection">
                        <div className="heroCont">
                            <h2>
                                ðŸ‘‹
                                <br />
                                I'm Akshansh, a developer and a life-long student.
                            </h2>
                            <p>
                                I started my coding journey in college, subsequently working as a freelance developer. I've worked internships and part-times, and with clients to oversee sprints and shipped experiences from envisioning through to launch.
                                <br /> <br />
                                I enjoy challenges and solving business and user problems by interfacing digital products. I want to spend my time developing solutions to make measureable differences in the lives of others.
                            </p>
                        </div>
                    </div>
    
                    <div className="certBadges">
                        <h2>Certifications &amp; Badges</h2>
                        <div className="imageCont">
                            {
                                data.allCertBadgesJson.edges.map((edge, index) => {
                                    const certBadgeItem = edge.node
    
                                    return (
                                        <div key={index} className="certBadgeItemCont">
                                            <div className="certBadgeImg">
                                                {
                                                    !!certBadgeItem.image && !!certBadgeItem.image.childImageSharp
                                                    ? <Img fluid={certBadgeItem.image.childImageSharp.fluid}
                                                        alt={"Certification / Badge Image"} />
                                                    : <img src={certBadgeItem.image.publicURL} alt={"Certification / Badge Image"} />
                                                }
                                            </div>
                                            <p className="certBadgeDesc">
                                                {certBadgeItem.description}
                                            </p>
                                        </div>
                                    )
                                })
                            }
                        </div>
                    </div>
                </Fade>
            </Container>
        )
    }
}

export default AboutPage


export const certBadgesQuery = graphql`
    query {
        allCertBadgesJson {
            edges {
                node {
                    description
                    image {
                        childImageSharp {
                            fluid {
                                ...GatsbyImageSharpFluid
                            }
                        }
                    }
                }
            }
        }
    }
`